import {OrderPartType} from "../../types";

interface Props {
  data: OrderPartType
}

const OrderPart:React.FC<Props> = (props) => {
  const {data} = props;

  return <div className="order-part">
    <p className="order-part__name"><b>:</b> {data.name}</p>
    <p className="order-part__qty"><b>-:</b> {data.qty}</p>
    <p className="order-part__price"><b>:</b> {data.price}</p>
    <p className="order-part__sum"><b>:</b> {data.sum}</p>
  </div>
};


export default OrderPart
